*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --main-color: #56E5CC;
  --secondary-color:#B0A4FF;
  --detail-color:#2F4858;
  --main-gradient:linear-gradient(to right top, #56e5cc, #00ddef, #00cfff, #69bcff, #b0a4ff);
}

.main{
  background-image: var(--main-gradient);
  min-height: 100vh;
  color: var(--detail-color);
  overflow-x: hidden;
}

li{
  list-style-type: none;
}

button{
  border: none;
  background-color: transparent;
}

/*#3E8A54 - #00776A - #05606C*/
/*Sections*/
.mainSection{
  width: 75%;
  min-width: 850px;
}
.secondarySection{
  width: 30%;
}
/*Posts*/
.postsContainer{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.post{
  width: 75%;
  margin: .5rem 0;
  border: none;
  overflow: hidden;
  border-radius: 10px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
  user-select:none;
  padding: 0;
  border: 1px solid  var(--detail-color);
}

.post:hover{ 
  border: 1px solid  var(--secondary-color);
}
.btn:hover{
  filter: contrast(125%);
  color: white;
}
.btnContainer{
  min-width: 35px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--secondary-color);
  flex-direction: column;
}
.btnOptionsContainer{
  padding: 0;
  margin: 0;
  height: 90px;
}
.btnContainer button{
  font-size: 20px;
  color: var(--detail-color);
}
.card-body{
  max-width: calc(100% - 65px);
  color: var(--detail-color);
  font-style: none;
  text-decoration: none;
  cursor: pointer;
}
.card-body:hover{
  color: var(--detail-color);
  font-style: none;
  text-decoration: none;
}
.card-text{
  max-height: 150px;
  overflow-y: auto;
}
.btnContainer button:hover:not(.Give-a-coffe){
  transform: scale(1.2);
}
.btnOptionsContainer button:hover::after{
  position: absolute;
  font-size: 13px;
  bottom: 3px;
  right: 28px;
  font-weight: 600;
  color: var(--detail-color);
}
.delete:hover::after{
  content: "Delete";
}
.edit:hover::after{
  content: "Edit";
}
.save:hover::after{
  content: "Save";
}
.unsave:hover::after{
  content: "Unsave";
}
/*Cup Styles*/
.Give-a-coffe svg{
  transform: rotate(0deg);
  transition: .2s;
}
.Give-a-coffe:hover svg{
  transform: scale(1.2);
  transition: .2s;
}

.Give-a-coffe:active svg{
  transform: rotate(-45deg) scale(1.2);
  transition: .1s;
}

.Give-a-coffe:active .Give-a-coffeCounter{
  transform: scale(.8);
  transition: .2s;
}
.Give-a-coffeCounter{
  position: absolute;
  right: 38px;
  bottom: 4px;
  font-size: 18px;
  transition: .3s;
}

.coffeeCounter{
  font-size: 18px;
}

.coffeeCounter svg{
  transform: scale(.8);
}

.post .card-title{
  min-height: 30px;
  max-height: 100px;
  overflow-y: auto;
}

/*Post Form*/
.btn-main{
  background-image: var(--main-gradient);
  color: white;
}
.openForm, .closeForm{
  border-radius: 5px;
}
.openFormState:hover .openForm, .openFormState:hover .closeForm, .openForm:hover, .closeForm:hover{
  padding: 1px;
  transition: .3;
  background-color: var(--secondary-color);
  transition: .3s;
  border-radius: 5px;
}
.openFormState:hover .openForm::after, .openForm:hover::After{
  position: absolute;
  font-size: 12px;  
  bottom: 14px;
  right: 35px;
  font-weight: 600;
  color: var(--detail-color);
  content: "Open Form";
}
.openFormState:hover .closeForm::after,.closeForm:hover::after{
  position: absolute;
  font-size: 12px;  
  bottom: calc(100% - 29px);
  right: 35px;
  font-weight: 600;
  color: var(--detail-color);
  content: "Close Form";
}

/*navBar*/
.navBar{
  position: fixed;
  width: 100%;
  z-index: 100;
  gap: 10px;
  display: flex;
  padding: 15px;
  color: var(--detail-color);
  background-color: var(--detail-color);
}
.navText{
  display: flex;
  padding: 10px;
  border-radius: 10px 0 0 10px;
  align-items: center;
  user-select: none;
  background: var(--main-gradient);
  color: var(--detail-color);
  text-decoration: none;
}
.navText:hover{
  text-decoration: none;
  color: var(--detail-color);
}
.navSearch{
  display: flex;
  padding: 10px;
  border-radius: 0 10px 10px 0;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-size: 20px;
  background: var(--main-gradient);
  width: 300px;

}

.navSearch input{
  background-color:var(--detail-color);
  border: none;
  color: white;
  border-radius: 5px;
  padding: 0 10px;
}

.searchDisplay{
  position: absolute;
  background-color: var(--detail-color);
  color: white;
  padding: 10px;
  width: 300px;
  top: 60px;
  z-index: 20;
  border: 5px solid var(--secondary-color);
  border-top: 0;
  border-radius: 0 0 10px 10px;
  user-select: none;
  max-height: 200px;
  overflow-x: auto;
}
a{
  color: var(--detail-color);
  text-decoration: none;
}
a:hover{
  color: var(--detail-color);
}
.searchDisplay h6{
  font-size: 14px;
  margin: 0;
}

.navBarFounded{
  color: white;
  font-size: 14px;
  margin: 0;
  padding: 0;
  width: 100%;
  text-align: start;
}
.navBarFounded:hover{
  background-color: var(--secondary-color);
}
.navSocailFunctions{
  width: auto;
  font-size: 15px;
  padding: 10px;
  background: var(--main-gradient);
}

.navSocailFunctions ul{
  display: flex;
  flex-direction: row;
  gap: 10px;
  padding: 0;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.navSocailFunctions ul div{
  display: flex;
  gap: 3px;
  user-select: none;
  cursor: pointer;
}

.navSocailFunctions svg{
  font-size: 20px;
}
/*selectedPost*/
.selectedPost{
  background-color: white;
  width: 90%;
  margin-top: 25px;
  max-width: 1300px;
  border-radius: 15px;
  color: var(--detail-color);
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
  overflow: hidden;
  border: none;
  margin-bottom: 20px;
}

.selectedPost .btnOptionsContainer{
  width: 15vw;
  min-width: 100px;
  max-width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  height: 100%;
}
.selectedPost svg{
  font-size: 20px;
}
.selectedPost .card-body{
  cursor: default;
}

.selectedPost .card-text{
  max-height: none;
}



